@import url(https://use.typekit.net/drz2vvu.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{--text: #ecebe8;--base--white: #ffffff;--base--black: #050505;--base--grey: #242424;--base--green: #62d36e;--error--red: #d8000c;--base--grey--dark: #131313;--base--green--dark: #1db954;--text--light: #ecebe8;--border--color: #828282;--border-radius--sm: 8px;--border-radius--lg: 50px;--border--thick: 4px;--spacer--sm: 20px;--spacer--lg: 60px;--text--xs: 13px;--text-sm: 18px;--text--md: 20px;--text--lg: 45px}html{scroll-behavior:smooth;box-sizing:border-box}*,*:before,*:after{box-sizing:inherit}body{-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-family:"proxima-nova", sans-serif;background:#131313;background:var(--base--grey--dark);color:#ffffff;color:var(--base--white)}a{color:#ffffff;color:var(--base--white);text-decoration:none}

.command{align-items:center;display:flex;height:100vh;justify-content:center;text-align:center}.command .command--input{border-radius:var(--border-radius--lg);font-size:17px;max-width:100%;padding:8px;width:546px}.command .command--actions{padding:var(--spacer--lg);display:flex;flex-direction:column}.command .command--actions .command--action{background:var(--base--green--dark);border:none;margin:var(--spacer--sm)}.command .command--action,.command .commmand-query{border-radius:var(--border-radius--lg);cursor:pointer;font:12px/15px "Proxima Nova", sans-serif;font-weight:600;width:102px;padding:3px 10px;text-transform:uppercase}.command .commmand-query{background:none;border:1px solid var(--base--white);color:var(--base--white);height:32px;margin-left:var(--spacer--sm)}@media only screen and (min-width: 1024px){.command .command--actions{flex-direction:row}}

